.item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70%;
  width: 100%;
  align-self: center;
}

img {
  height: 100%;
  max-width: 90%;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid lightblue; /* Blue */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}